import "./App.css";
import React, { useState } from "react";

function App() {
  const [temp, settemp] = useState(" ");
  const [humidity, sethumidity] = useState("");
  const [region, setregion] = useState("");
  const [lox, setlox] = useState("");
  const [loy, setloy] = useState("");
  const [wicon, setwicon] = useState("");
   

navigator.geolocation.getCurrentPosition(function (position) {
setlox(position.coords.latitude)
setloy(position.coords.longitude)

  });






  const baseurl = "http://api.weatherapi.com/v1";

  const url = `${baseurl}/current.json?key=daddb48675e645fbbef42243212405&q=${lox},${loy}`;
  console.log(url)
  fetch(url)
  .then((response) => response.json())
  .then((data) => {


      setregion(data.location.name);
      settemp(data.current.temp_c);
      sethumidity(data.current.humidity);
      setwicon(data.current.condition.icon);
    });

  return (
    <div className="App">
      <h1>Temprature: {temp}Â°</h1>
      <h1>Humidity: {humidity}%</h1>
      <h1>{region}</h1>

      <img src={wicon} alt=""></img>
    </div>
  );
}

export default App;
